	data() {
		return {
			stepsnum:0,   /*近31天总步数*/
			flowernum:0,
			sumflowers:0,  /*近31天总花数*/
			/* 日期时间对象 */
			year:null,
			month:null,
			day:null,
			hour:null,
			minute:null,
			week:null,
			accessNo:false,
			showStep:0,
			stepclass:"steps",
			plantnum:null,
			flowerWidth:100,
			flowerdonghua:null,
			flowernum:0,		/* 花数量 */
			donghuazt:0,		/* 代表动画状态,0是正在变大,1是正在变小 */
			allflowers:[],		/* 今日的花 */
			areaUsed:[		/* 区域的使用状况 */
				{
					id:0,
					used:false
				},
				{
					id:1,
